<template>
  <div class="page-msgbox">
    <h1 class="page-title">Message Box</h1>
    <div class="page-msgbox-wrapper">
      <mt-button @click.native="openAlert" size="large"> alert </mt-button>
      <mt-button @click.native="openConfirm" size="large"> confirm </mt-button>
      <mt-button @click.native="openPrompt" size="large"> prompt </mt-button>
    </div>
  </div>
</template>

<style>
  @component-namespace page {
    @component msgbox {
      @descendent wrapper {
        padding: 0 20px;
        position: absolute 50% * * *;
        width: 100%;
        transform: translateY(-50%);
        button:not(:last-child) {
          margin-bottom: 20px;
        }
      }
    }
  }
</style>

<script type="text/babel">
  import { MessageBox } from '../../src/index';

  export default {
    methods: {
      openAlert() {
        MessageBox.alert('!', '');
      },

      openConfirm() {
        MessageBox.confirm('?', '');
      },

      openPrompt() {
        MessageBox.prompt(' ', '').then(({ value }) => {
          if (value) {
            MessageBox.alert(` ${ value }`, '');
          }
        });
      }
    }
  };
</script>



