<template>
  <div class="page-radio">
    <div class="page-title">radio</div>

    <mt-radio
      class="page-part"
      title=""
      v-model="value1"
      :options="options1" />
    <div>
      <mt-cell title="">{{ value1 }}</mt-cell>
    </div>

    <mt-radio
      class="page-part"
      title=""
      v-model="value2"
      :options="options2" />
    <div>
      <mt-cell title="">{{ value2 }}</mt-cell>
    </div>

    <mt-radio
      align="right"
      class="page-part"
      title=""
      v-model="value3"
      :options="options3" />
  </div>
</template>

<script>
export default {
  name: 'page-radio',

  data() {
    return {
      value1: '',
      value2: 'A',
      value3: ''
    };
  },

  created() {
    this.options1 = ['A', 'B', 'C'];
    this.options3 = ['A', 'B', 'C'];
    this.options2 = [
      {
        label: '',
        value: 'F',
        disabled: true
      },
      {
        label: 'A',
        value: 'A'
      },
      {
        label: 'B',
        value: 'B'
      }
    ];
  }
};
</script>

<style lang="css">
  .page-radio .page-part {
    margin-top: 40px;
  }
</style>




