<template>
  <div class="page-toast">
    <h1 class="page-title">Toast</h1>
    <div class="page-toast-wrapper">
      <mt-button @click.native="openToast" size="large"> Toast</mt-button>
      <mt-button @click.native="openToastWithIcon" size="large"> icon  Toast</mt-button>
      <mt-button @click.native="openBottomToast" size="large"> Toast </mt-button>
    </div>
  </div>
</template>

<style>
  @component-namespace page {
    @component toast {
      @descendent wrapper {
        padding: 0 20px;
        position: absolute 50% * * *;
        width: 100%;
        transform: translateY(-50%);
        button:not(:last-child) {
          margin-bottom: 20px;
        }
      }
    }
  }
</style>

<script type="text/babel">
  import { Toast } from '../../src/index';

  export default {
    methods: {
      openToast() {
        Toast('');
      },

      openToastWithIcon() {
        Toast({
          message: '',
          iconClass: 'mintui mintui-success'
        });
      },

      openBottomToast() {
        Toast({
          message: '',
          position: 'bottom'
        });
      }
    }
  };
</script>



