<template>
  <div class="page-checklist">
    <div class="page-title">Checklist</div>

    <mt-checklist
      class="page-part"
      title=""
      v-model="value1"
      :options="options1">
    </mt-checklist>

    <div>
      <mt-cell title="">{{ value1 }}</mt-cell>
    </div>

    <mt-checklist
      class="page-part"
      title=""
      v-model="value2"
      :options="options2">
    </mt-checklist>

    <div>
      <mt-cell title="">{{ value2 }}</mt-cell>
    </div>

    <mt-checklist
      class="page-part"
      title=""
      :max="2"
      v-model="value3"
      :options="options3">
    </mt-checklist>

    <div>
      <mt-cell title="">{{ value3 }}</mt-cell>
    </div>

    <mt-checklist
      align="right"
      class="page-part"
      title=""
      v-model="value4"
      :options="options4">
    </mt-checklist>
  </div>
</template>

<script>
export default {
  name: 'page-checklist',

  data() {
    return {
      value1: [],
      value2: [''],
      value3: ['A'],
      value4: []
    };
  },

  created() {
    this.options1 = ['A', 'B', 'C'];
    this.options3 = ['A', 'B', 'C', 'D'];
    this.options4 = ['A', 'B', 'C', 'D'];
    this.options2 = [
      {
        label: '',
        value: 'F',
        disabled: true
      },
      {
        label: '',
        value: '',
        disabled: true
      },
      {
        label: 'A',
        value: 'A'
      },
      {
        label: 'B',
        value: 'B'
      }
    ];
  }
};
</script>

<style lang="css">
  .page-checklist .page-part {
    margin-top: 40px;
  }
</style>



